/**
 * Spinner.scss
 * Custom spinner with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
 @import '../../../base';

.spinner-container {
  width: 100%;
  height: 62px;
  transform: translateY(-2px);
  display: flex;
  border-radius: $border-radius-main;
  background-color: $color-form;
  border: solid 2px transparent;
  background-image: linear-gradient($color-form, $color-form);
  background-origin: border-box;
  background-clip: content-box, border-box;
  transition: all 0.3s ease;
  
  &:focus {
    outline: none;
  }

  &:focus,
  &:hover {
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.15);
    background-image: linear-gradient($color-form, $color-form),
      linear-gradient(101deg, $color-hover, $color-body);
  }

  .inner-container {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    position: relative;
    input {
      text-align:right;
      height: 100%;
      border: none;
      background: transparent;
      color: $color-hover;
      font-size: $font-size-regular;
      font-weight: $font-weight-bold;
      transform: translateX(-10px);
      
      &:focus {
        outline: none;
      }
    }

    .unit-container {
      height: 100%;
      display: flex;

      p {
        font-size: $font-size-regular;
        font-weight: $font-weight-bold;
        color:$color-title;
        text-align: left;
        margin: auto 0;
      }
    }
  }
  button {
    border: none;
    background: transparent;

    &:nth-of-type(1) {
      transform: translateX(20px);
      z-index: 1;
    }

    &:nth-of-type(2) {
      transform: translateX(-20px);
    }

    &:focus {
      outline: none;
    }

    &:hover {
      img {
        filter: $color-filter-hover;
      }
    }

    &:active {
      img {
        filter: $color-filter-title;
      }
    }
    
    img {
      filter: $color-filter-line;
    }
  }
}